.footer {
  padding: 40px 16px;
  background-color: $colorChocolate;

  &__content {
    display: flex;
    flex-direction: column;

    @include onDesktop {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-end;
      margin-bottom: 15px;
    }
  }

  &__logo {
    background-image: url(../images/logo/logo-footer.svg);
    background-size: cover;
    height: 55px;
    width: 112px;

    transition: transform 0.3s;

    &:hover {
      transform: scale(1.2);
    }

    margin-bottom: 60px;

    @include onDesktop {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 60px;

    @include onDesktop {
      margin-bottom: 0;
    }
  }

  &__nav-list {
    padding: 0 30px;
    display: flex;
    flex-direction: column;
    gap: 40px;

    @include onDesktop {
      flex-direction: row;
      gap: 34px;
    }
  }

  &__nav-item {
    list-style: none;
  }

  &__nav-link {
    color: $colorMilk;
    font-size: 16px;
    line-height: 150%;
    text-decoration: none;
    transition: color 0.3s;

    &:hover {
      color: $colorCroissant;
    }
  }

  &__icon-contact {
    display: flex;
    justify-content: center;
    gap: 66px;
    margin-bottom: 47px;

    @include onDesktop {
      gap: 20px;
      margin-bottom: 0;
    }
  }

  &__icon-con {
    height: 32px;
    width: 32px;
    background-repeat: no-repeat;
    transition: all 0.3s;

    &--twiter {
      background-image: url(../images/footer/twitter.svg);

      &:hover {
        background-image: url(../images/footer/twitter–hover.svg);
      }
    }

    &--linkedin {
      background-image: url(../images/footer/linkedin.svg);

      &:hover {
        background-image: url(../images/footer/linkedin-hover.svg);
      }
    }

    &--fb {
      background-image: url(../images/footer/facebook.svg);

      &:hover {
        background-image: url(../images/footer/facebook–hover.svg);
      }
    }
  }

  &__terms {
    display: flex;
    justify-content: center;
    gap: 30px;
    color: $colorMilk;
    font-size: 12px;
    opacity: 0.7;
  }

  &__terms-link {
    color: inherit;
    text-decoration: none;
  }
}
