.contact-us-form {
  padding: 36px 16px 54px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include onDesktop {
    padding: 100px;
    flex-direction: row-reverse;
    gap: 174px;
  }

  &__inputs {
    width: 100%;
    max-width: 460px;
  }

  &__title {
    text-align: center;

    margin-bottom: 20px;
  }

  &__form-inputs {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  &__name {
    @extend %contact-us-input;
  }

  &__mail {
    @extend %contact-us-input;
  }

  &__tel {
    @extend %contact-us-input;
  }

  &__text-area {
    min-height: 90px;
    resize: none;
    @extend %contact-us-input;
    // position: absolute;

    margin-bottom: 30px;

    @include onDesktop {
      margin-bottom: 38px;
    }

    &::placeholder {
      position: absolute;
      bottom: 0;
      padding-bottom: 2px;
    }
  }

  &__btn {
    @extend %btn-general;
    border: none;
    cursor: pointer;
  }

  &__image {
    width: 100%;
    max-width: 460px;
    
    @include onDesktop {
      max-height: 418px;
      max-width: 510px;
    }
  }
}
