.menu {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 35%;
  background-color: $colorCroissant;
  border-left: 3px solid $colorMilk;
  z-index: 3;
  padding: 32px 20px 120px;

  @include onTablet {
    padding: 110px 58px;
    bottom: 20%;
  }

  @include onDesktop {
    display: none;
  }

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
  }

  &__btn-link {
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }

  &__btn {
    background: url(../images/logo/menu-close.svg);
    background-size: cover;
    height: 22px;
    width: 22px;
  }

  &__item {
    width: min-content;
    position: relative;
    list-style: none;
  }

  &__link {
    @extend %nav-menu-link;

    &:after {
      background-color: $colorMilk;
    }
  }

  &__contact-us {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  &__tel {
    @extend %nav-menu-tel;
  }

  &__contact-btn {
    @extend %nav-menu-contact-btn;
  }

  &__language {
    width: max-content;
    background-color: $colorCroissant;
    border: none;
  }
}
