.header {
  background: url(../images/bake-cream-puff-cup-desktop.png) no-repeat center / cover;
  min-height: 100vh;
  position: relative;
  padding: 16px 16px 28px;

  @media (min-width: 640px) {
    padding: 100px 58px;
  }
  
  @include onTablet {
    min-height: 80vh;
  }

  @include onDesktop {
    display: flex;
    min-height: 60vh;
    justify-content: space-between;
    padding: 140px 58px;
  }

  @include onFullHd {
    min-height: 40vh;
  }

  &__content {
    position: absolute;
    @include onDesktop {
      position: relative;
      width: 1322px;
      margin: 0 auto;
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    z-index: 2;

    margin-bottom: 96px;

    @include onDesktop {
      max-width: 1322px;
      position: absolute;
      top: 34px;
      left: 68px;
      right: 68px;

      margin: 0 auto;
    }
  }

  &__logo {
    background-image: url(../images/logo/logo-header.svg);
    background-size: cover;
    height: 43px;
    width: 84px;

    transition: transform 0.3s;

    @include onDesktop {
      height: 55px;
      width: 112px;
    }

    &:hover {
      transform: scale(1.2);
    }
  }

  &__nav {
    display: none;

    @include onDesktop {
      display: inline;
    }
  }

  &__nav-list {
    display: flex;
    gap: 34px;
  }

  &__nav-item {
    list-style: none;
    position: relative;
  }

  &__nav-link {
    @extend %nav-menu-link;
  }

  &__menu-btn-link {
    padding-bottom: 10px;
    @include onDesktop {
      display: none;
    }
  }

  &__menu-btn {
    background-image: url(../images/logo/menu.svg);
    background-size: cover;
    height: 20px;
    width: 25px;
  }

  &__title {
    font-size: 100px;
    line-height: 80%;
    color: $colorChocolate;
    text-shadow: 1px 1px 20px white;

    margin-bottom: 29px;

    @include onDesktop {
      font-size: 160px;
      line-height: 177px;

      margin-bottom: 20px;
    }
  }

  &__text {
    font-size: 16px;
    line-height: 150%;
    text-shadow: 1px 1px 20px white;

    margin-bottom: 20px;

    @include onDesktop {
      font-size: 26px;
      line-height: 150%;
    }
  }

  &__btn {
    @extend %btn-general;
  }

  &__pages {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 35px;
    display: flex;
    align-items: flex-end;
    gap: 20px;
    z-index: 1;

    @include onDesktop {
      background-color: $colorCroissant;
      left: auto;
      top: 0;
      right: 0;
      bottom: 0;
      transform: translateX(0);
      width: 425px;
      flex-direction: column;
      justify-content: center;
      padding-right: 45px;
    }
  }

  &__pages-row-up {
    @include onDesktop {
      margin-right: 25px;
      height: 18px;
      width: 30px;
      transition: background-image 0.3s;
      background: url(../images/arrow/arrow-up.svg) no-repeat center / cover;
    }

    &:hover {

      background-image: url(../images/arrow/arrow-up-hover.svg);
    }
  }

  &__pages-row-down {
    @include onDesktop {
      margin-right: 25px;
      height: 18px;
      width: 30px;
      transition: background-image 0.3s;
      background: url(../images/arrow/arrow-down.svg) no-repeat center / cover;
    }

    &:hover {
      background-image: url(../images/arrow/arrow-down-hover.svg);
    }
  }

  &__item {
    font-size: 16px;
    line-height: 32px;
    color: $colorCroissant;
    text-decoration: none;
    text-shadow: 1px 1px 6px $colorCream;
    transition: color 0.3s;

    &:hover {
      color: $colorChocolate;
    }

    @include onDesktop {
      font-size: 40px;
      line-height: 47px;
      color: $colorCream;
    }

    &--active {
      font-size: 40px;
      line-height: 47px;
      color: $colorChocolate;

      @include onDesktop {
        font-size: 176px;
        line-height: 209px;
      }
    }
  }

  &__contact-us {
    display: none;

    @include onDesktop {
      display: flex;
      gap: 20px;
    }
  }

  &__tel {
    @extend %nav-menu-tel;
  }

  &__contact-btn {
    @extend %nav-menu-contact-btn;
  }

  &__language {
    background-color: inherit;
    border: none;
    cursor: pointer;
  }
}
