.bakershops {
  padding: 40px 10px 30px;
  background-color: $colorCream;

  &__title {
    @include onDesktop {
      margin-bottom: 96px;
    }
  }

  &__form {
    max-width: 600px;
    display: flex;
    flex-direction: column;
    gap: 25px;

    @include onDesktop {
      flex-direction: row;

      margin-bottom: 55px;
    }
  }

  &__chose-city {
    background-color: $colorCream;
    border: none;
    border-bottom: 1px solid $colorChocolate;
    opacity: 0.5;
    font-size: 16px;
    line-height: 150%;
    color: $colorChocolate;
    padding-bottom: 10px;
    outline: none;

    @include onDesktop {
      min-width: 174px;
    }
  }

  &__city-lviv {
    position: absolute;
    @extend %sizeCity6;
    top: 21%;
    left: 7%;
    &--6 {
      @include city(6, Lviv);
    }
  }

  &__city-lutsk {
    position: absolute;
    @extend %sizeCity4;
    top: 7%;
    left: 16%;
    &--4 {
      @include city(4, Lutsk);
    }
  }

  &__city-zhutomyr {
    position: absolute;
    @extend %sizeCity3;
    top: 16%;
    left: 33%;
    &--3 {
      @include city(3, Zhutomyr);
    }
  }

  &__city-vinnytsya {
    position: absolute;
    @extend %sizeCity4;
    top: 39%;
    left: 34%;
    &--4 {
      @include city(4, Vinnytsya);
    }
  }

  &__city-kyiv {
    position: absolute;
    @extend %sizeCity9;
    top: 20%;
    left: 43%;
    &--9 {
      @include city(9, Kyiv);
    }
  }

  &__city-chernihiv {
    position: absolute;
    @extend %sizeCity4;
    top: 7%;
    left: 52%;
    &--4 {
      @include city(4, Chernihiv);
    }
  }

  &__city-poltava {
    position: absolute;
    @extend %sizeCity2;
    top: 27%;
    left: 64%;
    &--2 {
      @include city(2, Poltava);
    }
  }

  &__city-kharkiv {
    position: absolute;
    @extend %sizeCity6;
    top: 27%;
    left: 78%;
    &--6 {
      @include city(6, Kharkiv);
    }
  }

  &__city-dnipro {
    position: absolute;
    @extend %sizeCity4;
    top: 43%;
    left: 70%;
    &--4 {
      @include city(4, Dnipro);
    }
  }

  &__city-kherson {
    position: absolute;
    @extend %sizeCity2;
    top: 66%;
    left: 64%;
    &--2 {
      @include city(2, Kherson);
    }
  }

  &__adress {
    background-color: $colorCream;
    border: none;
    border-bottom: 1px solid $colorChocolate;
    opacity: 0.5;
    font-size: 16px;
    line-height: 150%;
    color: $colorChocolate;
    padding-bottom: 10px;
    outline: none;
    margin-bottom: 25px;

    @include onDesktop {
      margin-bottom: 0;
      min-width: 322px;
    }
  }

  &__btn {
    @extend %btn-general;
    border: none;
    margin: 0 auto;
    margin-bottom: 25px;
    cursor: pointer;

    @include onDesktop {
      margin-bottom: 0;
      min-width: 160px;
    }
  }

  &__image-conteiner {
    font-family: "Rubik", sans-serif;
    position: relative;
    max-height: 688px;
    max-width: 1020px;

    margin-bottom: 20px;
  }

  &__image {
    width: 100%;
    height: 100%;
    border: none;
  }

  &__size {
    min-width: 120px;
    display: flex;
    justify-content: center;
    gap: 40px;

    @include onDesktop {
      display: none;
    }
  }

  &__size-small {
    height: 45px;
    width: 40px;
    background-image: url(../images/arrow/-.svg);
  }

  &__size-big {
    height: 45px;
    width: 40px;
    background-image: url(../images/arrow/+.svg);
  }
}
