.contact-us {

  // @include onDesktop {
  //   display: flex;
  // }

  &__title {
    color: $colorMilk;

    // margin-bottom: 20px;

    &--1 {
      display: none;
      color: $colorCroissant;
      margin-bottom: 26px;

      @include onDesktop {
        display: block;
      }
    }

    &--2 {
      @include onDesktop {
        display: none;
      }
    }
  }

  &__wrapper {
    background-color: $colorCroissant;
  }

  &__filler {
    @include onDesktop {
      display: flex;
    }
  }

  &__content {
    padding: 40px 16px;
    background-color: $colorCroissant;

    @include onDesktop {
      width: 30%;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
  }

  &__item {
    display: flex;
    align-items: center;
    gap: 20px;
    list-style: none;

    &:not(:last-child) {
      margin-bottom: 9px;
    }

    &:nth-child(n+4) {
      margin-left: 38px;
    }
  }

  &__link-icon {
    height: 20px;
    width: 18px;
    background-repeat: no-repeat;
    &--1 {
      background-image: url(../images/contact-us/item-1.svg);
    }

    &--2 {
      background-image: url(../images/contact-us/item-2.svg);
    }

    &--3 {
      background-image: url(../images/contact-us/item-3.png);
      background-size: contain;
    }
  }

  &__link {
    color: $colorChocolate;
    transition: color 0.3s;

    &:hover {
      color: rgba(255, 255, 255, 0.75);
    }
  }

  &__map {
    width: 100%;
  }
}
