.about {
  padding: 40px 0;
  &__top {
    padding: 0 16px 20px;
  }

  @include onDesktop {
    margin-bottom: 120px;
  }

  &__subtitle {
    font-size: 16px;
    line-height: 150%;
    text-align: center;
    color: #000;

    margin-bottom: 20px;

    &--1 {
      @include onDesktop {
        display: none;
      }
    }

    &--2 {
      display: none;
      @include onDesktop {
        display: block;
      }
    }
  }

  &__cards {
    display: flex;
    justify-content: space-between;
    gap: 27px;
    padding: 0 86px;
    overflow: auto;

    @include onDesktop {
      gap: 80px;
      padding: 0;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    align-items: center;

    padding-bottom: 10px;
  }

  &__image {
    height: 150px;
    width: 150px;

    @include onDesktop {
      height: 200px;
      width: 200px;
    }

    margin-bottom: 20px;
  }

  &__card-title {
    font-weight: 300;
    font-size: 60px;
    line-height: 71px;
    text-align: center;
    color: $colorChocolate;

    margin-bottom: 7px;
  }
}
