.bakery {
  padding: 38px 16px;

  &__title {
    margin-bottom: 20px;
  }

  &__content {
    width: 100%;
    display: flex;
    flex-direction: column;

    @include onDesktop {
      width: max-content;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;

    &:last-child {
      margin-bottom: 48px;
      @include onDesktop {
        margin-bottom: 168px;
      }
    }

    @include onDesktop {

      flex-direction: row;
      justify-content: space-between;

      &:nth-child(2n) {
        flex-direction: row-reverse;
      }
    }
  }

  &__subtitle {
    font-size: 35px;
    line-height: 41px;
    color: $colorCroissant;

    margin-bottom: 15px;

    @include onDesktop {
      font-size: 50px;
      line-height: 59px;
      margin-bottom: 44px;
    }
  }

  &__name {
    font-size: 20px;
    line-height: 150%;
    color: $colorChocolate;

    margin-bottom: 12px;

    @include onDesktop {
      font-size: 26px;
      line-height: 150%;
    }
  }

  &__text {
    font-size: 16px;
    line-height: 150%;
    color: $colorChocolate;

    margin-bottom: 20px;

    @include onDesktop {
      margin-bottom: 32px;
    }
  }

  &__items {
    display: flex;
    flex-direction: column;

    @include onDesktop {
      gap: 135px;
    }
  }

  &__price {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 65px;
    width: 65px;
    background-color: $colorCream;
    border-radius: 50%;
    transition: transform 0.6s;
    cursor: pointer;

    @include onDesktop {
      flex-direction: row;
      height: 110px;
      width: 110px;
    }
  }

  &__content:hover &__price {
    transform: rotateY(360deg);
  }

  &__price-text {
    font-weight: bold;
    text-align: center;
  }

  &__flesh {
    display: none;

    @include onDesktop {
      display: inline;
    }
  }

  &__image-container {
    max-height: 514px;
    max-width: 700px;

    @include onDesktop {
      transform: translateY(40px);
    }
  }

  &__image {
    height: 100%;
    width: 100%;
    transition: transform 0.3s;

    &:hover {
      @include onDesktop {
        transform: scale(0.9);
      }
    }
  }

  &__btn {
    margin: 0 auto;
    @extend %btn-general;
  }
}
